html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  overflow-x: hidden;
  margin: 0;
  color: #f7f7f7;
  background-color: #282828;
  font-family: 'Montserrat', sans-serif; }

h1, h2, h3 {
  font-family: 'Inconsolata', monospace, 'Tahoma', sans-serif;
  font-weight: 400; }

a {
  color: #97e0ef;
  text-decoration: none;
  transition: color 0.25s; }

a:hover {
  color: #b5e25a; }

